@import "vars";
@import "mixins";

.welcome {
  padding: 164px 0 160px;
  background-color: $background-gray;

  &__block {
    margin: auto;
    max-width: 742px;
  }

  &__title {
    @include section-title;
  }

  &__subtitle {
    font-size: 24px;
    line-height: 36px;
    color: $heavy-blue;
  }
}
